.cars {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.title {
  font-family: 'Poppins';
  font-weight: 800;
  font-size: 45px;
}

body {
  font-family: 'Poppins';
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .cars {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      position: relative;
      right: 20px;
    }
  }