/* fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

/* styles */

.container-fluid img {
    width: 100px;
}

.navigation {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    height: 100px;
}

.nav-item a {
    text-decoration: none;
    margin-left: 120px;
    color: black;
    padding: 10px;
    font-family: 'Poppins', sans-serif;
}

.nav-item a:hover {
    background-color: rgb(230, 229, 229);
}